  text-align: left;
}
.left {
  line-height: 40px;
}
.el-row {
  height: 40px;
  align-items: center;
  display: flex;
  margin-bottom: 15px;
}
.input {
  width: 120px;
  margin-right: 15px;
}
.input2 {
  width: 220px;
  margin-right: 15px;
}
</style>