<template>
  <div class="login-wrap">
    <el-dialog 
    :visible.sync="dialogVisible"
    width="30%"
    class=""
    :show-close="false"
    > 
     <div class="islogin" v-if="islogin">
        <el-form label-position="left" :model="ruleForm" :rules="rules" ref="ruleForm" label-width="0px"
          class="demo-ruleForm login-container">
          <h3 class="title">用户登录</h3>
          <el-form-item prop="username">
            <el-input type="text" v-model="ruleForm.username" placeholder="账号"></el-input>
          </el-form-item>
          <el-form-item prop="password">
            <el-input type="password" v-model="ruleForm.password" placeholder="密码"></el-input>
          </el-form-item>
          <!-- <el-checkbox class="remember" v-model="rememberpwd">记住密码</el-checkbox> -->
          <el-form-item style="width:100%;">
            <el-button type="primary" style="width:100%;" @click="submitForm('ruleForm')" :loading="logining">登录</el-button>
          </el-form-item>
          <el-form-item>
              <span @click="islogin=false">注册</span>
          </el-form-item>
        </el-form>
      </div>
      <div class="isreg" v-else>
          <el-form label-position="left" :model="ruleForm" :rules="rules" ref="ruleForm" label-width="0px"
            class="demo-ruleForm login-container">
            <h3 class="title">注册</h3>
            <el-form-item prop="username">
              <el-input type="text" v-model="ruleForm.username" placeholder="账号"></el-input>
            </el-form-item>
            <el-form-item prop="password">
              <el-input type="password" v-model="ruleForm.password" placeholder="密码"></el-input>
            </el-form-item>
            <!-- <el-checkbox class="remember" v-model="rememberpwd">记住密码</el-checkbox> -->
            <el-form-item style="width:100%;">
              <el-button type="primary" style="width:100%;" @click="submitForm('ruleForm')" :loading="logining">注册</el-button>
            </el-form-item>
            <el-form-item>
                <span @click="islogin = true">登陆</span>
            </el-form-item>
          </el-form>
        </div>
    </el-dialog>
    
  </div>
</template>
<script>

import {
  Login,
} from '@/api/user'
export default {
  name: 'login',
  data() {
    return {
      dialogVisible:false,
      //定义loading默认为false
      logining: false,
      // 记住密码
      rememberpwd: false,
      ruleForm: {
        //username和password默认为空
        username: '',
        password: '',
        code: '',
        randomStr: '',
        codeimg: ''
      },
      //rules前端验证
      rules: {
        username: [{ required: true, message: '请输入账号', trigger: 'blur' }],
        password: [{ required: true, message: '请输入密码', trigger: 'blur' }],
        code: [{ required: true, message: '请输入验证码', trigger: 'blur' }]
      },
      islogin: true,

    }
  },
  created() {
   

  },
  // 里面的函数只有调用才会执行
  methods: {
    open() {
      this.dialogVisible = true
    },
    //获取info列表
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          Login({
            username: this.ruleForm.username,
            password: this.ruleForm.password
          }).then(res => {
            if (res.success) {
              this.logining = true
              // 测试通道，不为空直接登录
              setTimeout(() => {
                this.logining = false
               
            
              }, 1000)
            } else {
              this.$message({
                type: 'info',
                message: res.msg
              })
            }

          })
        } 
      })
    },
  }
}
</script>

